export default async function getUserInfo(that) {
    uni.showModal({
        title: '',
        content: '',

        success(res) {
            if (res.confirm) {
                uni.getUserProfile({
                    desc: '',
                    // 
                    success: (res) => {
                        that.setData({
                            userInfo: res.userInfo
                        });
                        uni.setStorageSync('userInfo', res.userInfo);
                        console.log(res.userInfo);
                        that.setData({
                            userInfo: res.userInfo
                        });
                        that.setUserInfo(res.userInfo);
                    },
                    fail: function (err) {
                        console.log(err);
                    }
                });
            }
        }
    });
}
