const BASE_URL = 'http://www.wangyixuan2002.cn:8079';

class WRequset {
    request(url, method, params) {
        return new Promise((resolve, reject) => {
            uni.request({
                url: BASE_URL + url,
                method,
                header: {
                    //
                    'content-Type': 'application/json' // 'Authorization':wx.getStorageSync("key"),//token//token
                },
                data: params,
                success: (res) => {
                    // console.log(res)
                    resolve(res);
                },
                fail: (err) => {
                    reject(err);
                }
            });
        });
    }

    get(url, params) {
        return this.request(url, 'GET', params);
    }

    post(url, data) {
        return this.request(url, 'POST', data);
    }
}

const wRequest = new WRequset();
export default wRequest;
